.uvInfo {
	text-align: center;
	padding-top: 60px;
	font-family: 'Satisfy', cursive;
	color: rgb(0, 53, 97);
}

.modal {
	position: fixed;
	z-index: 500;
	background-color: white;
	width: 70%;
	border: 1px solid #ccc;
	box-shadow: 1px 1px 1px black;
	padding: 16px;
	left: 15%;
	top: 30%;
	box-sizing: border-box;
	transition: all 0.3s ease-out;
}
.modal h2 {
	border-bottom: 1px solid #ccc;
	padding: 1rem;
	margin: 0;
}

.content {
	padding: 1rem;
}
.actions {
	border-top: 1px solid #ccc;
	background: #eee;
	padding: 0.5rem 1rem;
}

.mainContainer {
	background-image: url('https://i.pinimg.com/originals/59/f9/b2/59f9b2ccf585d1206c6c6768fbdedfe6.jpg');
	background-size: cover;
	background-repeat: no-repeat;
	width: 100%;
	position: absolute;
	top: 0;
	left: 0;
	padding-bottom: 600px;
}

.headerContainer {
